@use "../globals";

// stylelint-disable declaration-no-important


// Shadows

@if globals.$feature-shadows {
  .shadow-sm {
 box-shadow: globals.$box-shadow-sm !important;
 }
  .shadow {
 box-shadow: globals.$box-shadow !important;
 }
  .shadow-lg {
 box-shadow: globals.$box-shadow-lg !important;
 }
  .shadow-none {
 box-shadow: none !important;
 }
}
