@use "sass:map";
@use "../../scss/mixins";
@use "../../scss/functions";
@use "../../scss/globals";


// Shape
// --------------------------------------------------

@use "shape.vars" as *;

.shape-square {
  /**
   * @prop --border-radius: Border radius of shape
   */
  --border-radius: #{$border-radius};

  @include mixins.border-radius-set(var(--border-radius));
  display: inline-flex;

  position: relative;

  align-items: center;
  justify-content: center;

  overflow: hidden;
}


// Circle

.shape-circle {
  /**
   * @prop --border-radius: Border radius of shape
   */
  --border-radius: 50%;

  @include mixins.border-radius-set(var(--border-radius));
  display: inline-flex;

  position: relative;

  align-items: center;
  justify-content: center;

  overflow: hidden;
}


// Sizes
// --------------------------------------------------

@each $breakpoint in map.keys(globals.$breakpoint-list) {
  @include mixins.breakpoint-media-breakpoint-up($breakpoint) {
    $infix: mixins.breakpoint-breakpoint-infix($breakpoint, globals.$breakpoint-list);

    @each $size, $value in $sizes {
      .shape#{$infix}-#{$size} {
        width: $value;
        height: $value;
      }
    }
  }
}
