@use "./position";
@use "./margin";

// Hide


// Cover input

@mixin cover() {
  @include position.set(0, null, null, 0);
  @include margin.set(0);

  position: absolute;

  width: 100%;
  height: 100%;

  border: 0;

  outline: none;

  background: transparent;

  cursor: pointer;
  appearance: none;

  &::-moz-focus-inner {
    border: 0;
  }
}


// Hide visually

@mixin visually() {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  width: 100%;
  height: 100%;

  margin: 0;

  padding: 0;

  border: 0;

  outline: 0;

  opacity: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  appearance: none;
}
