@use "../../scss/mixins";
@use "../../scss/functions";
@use "../../scss/globals";


// Nav
// --------------------------------------------------

@use "nav.vars" as *;

.nav {
  @include mixins.padding-set($padding-y, $padding-x);

  display: block;

  flex: 1;

  color: $color;

  font-weight: $font-weight;

  text-align: center;

  &:hover,
  &:focus {
    color: $hover-color;
  }

  // Disabled state lightens text
  &.disabled {
    color: $disabled-color;

    cursor: default;
    pointer-events: none;
  }
}
