@use "../../scss/mixins";
@use "../../scss/functions";
@use "../../scss/globals";


// Alert
// --------------------------------------------------

@use "alert.vars" as *;

.alert {
  /**
   * @prop --background: Background of the alert
   *
   * @prop --min-width: Minimum width of the alert
   * @prop --width: Width of the alert
   * @prop --max-width: Maximum width of the alert
   *
   * @prop --min-height: Minimum height of the alert
   * @prop --height: Height of the alert
   * @prop --max-height: Maximum height of the alert
   *
   * @prop --backdrop-opacity: Opacity of the backdrop
   */
  --min-width: #{$alert-min-width};
  --width: calc(100% - #{globals.$spacer});
  --min-height: auto;
  --height: auto;
  --max-height: #{$alert-max-height};
  --background: #{$alert-background-color};
  --max-width: #{$alert-max-width};
  --backdrop-opacity: var(--bkkr-backdrop-opacity, .3);
  --margin-bottom: calc(#{globals.$spacer} / 2);

  @include mixins.text-font-smoothing();
  @include mixins.position-set(0, 0, 0, 0);

  display: flex;

  position: absolute;

  align-items: flex-end;
  justify-content: center;

  outline: none;

  font-family: globals.$font-family-base;

  font-size: $alert-font-size;

  contain: strict;
  touch-action: none;
  user-select: none;
  z-index: globals.$z-index-overlay;
}

:host(.overlay-hidden) {
  display: none;
}

:host(.alert-top) {
  @include mixins.padding-set(50px, null, null, null);

  align-items: flex-start;
}

.alert-wrapper {
  @include mixins.border-radius-set($alert-border-radius);
  display: flex;

  flex-direction: column;

  width: var(--width);
  min-width: var(--min-width);
  max-width: var(--max-width);

  height: var(--height);
  min-height: var(--min-height);
  max-height: var(--max-height);

  margin-bottom: var(--margin-bottom);

  background: var(--background);

  box-shadow: $alert-box-shadow;

  contain: content;
  opacity: 0;
  overflow: hidden;
  z-index: globals.$z-index-overlay-wrapper;
}

.alert-head {
  @include mixins.padding-set($alert-head-padding-top, $alert-head-padding-end, $alert-head-padding-bottom, $alert-head-padding-start);
  text-align: $alert-head-text-align;
}

.alert-title {
  @include mixins.margin-set($alert-title-margin-top, null, null, null);
  @include mixins.padding-set(0);
  color: $alert-title-color;

  font-size: $alert-title-font-size;
  font-weight: $alert-title-font-weight;
}

.alert-sub-title {
  @include mixins.margin-set(5px, 0, 0);
  @include mixins.padding-set(0);
  color: $alert-sub-title-text-color;

  font-size: $alert-sub-title-font-size;
  font-weight: 400;

}

.alert-message {
  @include mixins.padding-set($alert-message-padding-top, $alert-message-padding-end, $alert-message-padding-bottom, $alert-message-padding-start);
  color: $alert-message-text-color;

  font-size: $alert-message-font-size;

  text-align: $alert-message-text-align;
  box-sizing: border-box;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  overscroll-behavior-y: contain;

}

.alert-checkbox-group::-webkit-scrollbar,
.alert-radio-group::-webkit-scrollbar,
.alert-message::-webkit-scrollbar {
  display: none;
}

.alert-input {
  @include mixins.padding-set(10px, 0);

  width: 100%;

  border: 0;

  background: inherit;

  font: inherit;
  box-sizing: border-box;
}

.alert-button-group {
  @include mixins.margin-set-horizontal(null, -$alert-button-border-width);
  display: flex;

  flex-direction: row;
  flex-wrap: $alert-button-group-flex-wrap;

  width: 100%;

}

.alert-button-group-vertical {
  flex-direction: column;
  flex-wrap: nowrap;
}

.alert-button {
  @include mixins.margin-set($alert-button-margin);
  @include mixins.border-radius-set($alert-button-border-radius);

  display: block;

  flex: $alert-button-flex;

  min-width: $alert-button-min-width;
  height: $alert-button-min-height;

  border: 0;

  border-top: $alert-button-border-width $alert-button-border-style $alert-button-border-color;
  border-right: $alert-button-border-width $alert-button-border-style $alert-button-border-color;

  background-color: $alert-button-background-color;
  color: $alert-button-text-color;

  font-size: $alert-button-font-size;

  line-height: $alert-button-line-height;

  overflow: hidden;
  z-index: 0;
}

.alert-button:first-child {
  border-right: 0;
}

.alert-button:last-child {
  border-right: $alert-button-border-width $alert-button-border-style $alert-button-border-color;

  font-weight: $alert-button-main-font-weight;
}

.alert-button.bkkr-activated {
  background-color: $alert-button-background-color-activated;
}

.alert-button.bkkr-focused,
.alert-tappable.bkkr-focused {
  background: globals.$theme-background-color-step-100;
}

.alert-button-inner {
  display: flex;

  flex-flow: row nowrap;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;
}

.alert-button .alert-button-inner {
  pointer-events: none;
}


// Alert Button: Disabled
// --------------------------------------------------
.alert-input-disabled,
.alert-checkbox-button-disabled .alert-button-inner,
.alert-radio-button-disabled .alert-button-inner {
  pointer-events: none;

  cursor: default;
  opacity: .5;
}

.alert-tappable {
  @include mixins.margin-set(0);
  @include mixins.padding-set(0);
  display: flex;

  width: 100%;

  border: 0;

  background: transparent;

  font-size: inherit;

  line-height: initial;

  text-align: start;

  contain: strict;
  appearance: none;
}

.alert-button,
.alert-checkbox,
.alert-input,
.alert-radio {
  outline: none;
}

.alert-radio-icon,
.alert-checkbox-icon,
.alert-checkbox-inner {
  box-sizing: border-box;
}

// stylelint-disable-next-line selector-no-qualifying-type
textarea.alert-input {
  min-height: $alert-input-min-height;
  resize: none;
}


// Button: Destructive
// ---------------------------------------------------

.alert-button-role-destructive,
.alert-button-role-destructive.ion-activated,
.alert-button-role-destructive.ion-focused {
  color: $alert-button-destructive-text-color;
}
