// Navs
// --------------------------------------------------

@use "navs.vars" as *;

.navs {
  @include mixins.padding-set(null, null, null, 0);


  display: flex;

  flex-wrap: nowrap;
  align-items: center;
  justify-content: stretch;

  height: 100%;

  margin-bottom: 0;

  list-style: none;
}

