@use "./position";

// Element states

// Focus

@mixin focus() {
  &:focus,
  &.focus {
    @content;
  }
}

// Hover

@mixin hover() {
  @media (any-hover: hover) {
    &:hover {
      @content;
    }
  }
}

// Active

@mixin active() {
  &:active {
    @content;
  }
}

// Disabled

@mixin disabled() {
  &:disabled,
  &.disabled {
    @content;
  }
}

// Button

@mixin button() {
  @include position.set(0, 0, 0, 0);

  position: absolute;

  content: "";

  opacity: 0;
}
