@use "../../scss/mixins";
@use "../../scss/functions";
@use "../../scss/globals";


// Radio
// --------------------------------------------------

@use "radio.vars" as *;

:host {
  /**
   * @prop --color: Color of the radio
   * @prop --color-checked: Color of the checked radio
   * @prop --border-radius: Border radius of the radio
   * @prop --inner-border-radius: Border radius of the inner checked radio
   */
  --inner-border-radius: 50%;

  display: inline-block;
  position: relative;
  box-sizing: border-box;

  user-select: none;

  z-index: globals.$z-index-item-input;
}

:host(.radio-disabled) {
  pointer-events: none;
}

.radio-icon {
  display: flex;

  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  contain: layout size style;
}

.radio-icon,
.radio-inner {
  box-sizing: border-box;
}

label {
  @include mixins.hide-cover();

  display: flex;

  align-items: center;

  opacity: 0;
}

/* input {
  @include mixins.hide-visually();
} */

:host(:focus) {
  outline: none;
}

// Radio Within An Item
// -----------------------------------------

:host(.in-item) {
  @include mixins.margin-set($item-end-margin-top, $item-end-margin-end, $item-end-margin-bottom, $item-end-margin-start);

  display: block;
  position: static;
}

:host(.in-item[slot="start"]) {
  @include mixins.margin-set($item-start-margin-top, $item-start-margin-end, $item-start-margin-bottom, $item-start-margin-start);
}