@use "../../scss/mixins";
@use "../../scss/functions";
@use "../../scss/globals";


// Slide
// --------------------------------------------------

.slide {
  display: flex;
  position: relative;

  flex-shrink: 0;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  font-size: 18px;

  text-align: center;
  box-sizing: border-box;
}
